
import axios from 'axios';
import { mapGetters } from 'vuex';
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import wx from 'weixin-js-sdk';
import weixin from '@/services/weixin';
import { setTimeout } from 'timers';
export default {
directives: {
TransferDom
},
components: {
XHeader,
GroupTitle,
Flexbox,
FlexboxItem,
XButton,
DatetimePlugin,
Datetime,
Group,
Picker,
Alert,
Previewer,
Popup,
},
data() {
return {
showPop:false,
name1: '',
value1: '',
startTime: '',
endTime: '',
hourListValue: '',
hot: '',
ListValue: '',
list1: [{ text: '口头汇报', moduleId: 11 }, { text: '书面汇报', moduleId: 12 }],
list1Selected: {},
activeContent: '',
listSingle: {},
userName: '',
picList: { list: [], arr: [] }
};
},
methods: {
showMenu(){
this.showPop = true;
},
know(){
this.showPop = false;
},
getUser1() {
axios
.get('ppartymember/queryByUserId', {
params: {
userid: this.$route.params.userId
}
})
.then(res => {
this.name1 = res.data.name;
})
.catch(err => {
console.log(err);
});
},
showDeleteButton(it) {
clearInterval(this.Loop); 
var This = this;
this.Loop = setTimeout(function() {
This.picList.list.splice(it, 1);
This.picList.arr.splice(it, 1);
This.$vux.alert.show({ title: '删除成功' });
}, 1000);
},
clearLoop() {
clearInterval(this.Loop);
},
atBig(item) {},
getList() {
axios
.get('pscoredetail/queryById', {
params: {
id: this.$route.params.moduleid
}
})
.then(res => {
this.listSingle = res.data;
})
.catch(err => {
console.log('fail' + err.data);
});
},
getUser() {
axios
.get('ppartymember/queryByUserId', {
params: {
userid: this.user.userid
}
})
.then(res => {
this.userName = res.data.name;
})
.catch(err => {
console.log(err);
});
},
openPicker() {
this.$refs.picker.open();
},
submit() {
var starttime = this.startTime.replace(new RegExp('-', 'gm'), '/');
var starttimeHaoMiao = new Date(starttime).getTime();
if (!starttime) {
return this.$vux.toast.show({
text: '请设置时间',
type: 'text'
});
}
if (!this.activeContent) {
return this.$vux.toast.show({
text: '请填写内容',
type: 'text'
});
}
if (!this.picList.arr.length) {
return this.$vux.toast.show({
text: '请上传图片',
type: 'text'
});
}
axios({
url: 'pscoreparty/scoreCustom',
method: 'post',
params: {
detailId: this.list1Selected.moduleId,
userId: this.$route.params.userId,
adderId: this.user.userid,
score: 5,
imgs: this.picList.arr.join(),
remark: this.activeContent,
addTimes: new Date(this.startTime.replace(/-/gi, '/')).getTime()
}
})
.then(res => {
console.log(res);
if (res.success) {
this.$vux.alert.show({ title: '增加成功',onHide(){
setTimeout(() => history.back(), 1000);
}});
} else {
this.$vux.alert.show({ title: res.msg });
}
})
.catch(err => {
if (err.success) {
this.$vux.alert.show({ title: '增加失败' });
setTimeout(() => {
this.$vux.alert.hide();
}, 1000);
}
});
},

getActivity() {},
log(str1, str2 = '') {
console.log(str1, str2);
},
showPlugin() {
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD HH',
value: '2017-05-20 18',
onConfirm(val) {
console.log('plugin confirm', val);
},
onShow() {
console.log('plugin show');
},
onHide() {
console.log('plugin hide');
}
});
},
toggleFormat() {
this.format = this.format === 'YYYY-MM-DD HH:mm' ? 'YYYY-MM-DD' : 'YYYY-MM-DD HH:mm';
},
changeStart(value) {
this.startTime = value;
},
changeEnd(value) {
this.endTime = value;
},
clearValue(value) {
this.value6 = '';
},
clearValue8(value) {
this.value8 = '';
},
setToday(value) {
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = '0' + cmonth;
if (day < 10) day = '0' + day;
this.value7 = now.getFullYear() + '-' + cmonth + '-' + day;
console.log('set today ok');
},

chooseImage(it) {
wx.chooseImage({
count: 3, // 默认9
sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: res => {
let localIds = res.localIds || [];
new Promise(resolve => {
let serverIds = [];
let toUpload = localId =>
wx.uploadImage({
localId: localId, // 需要上传的图片的本地ID，由chooseImage接口获得
isShowProgressTips: 1, // 默认为1，显示进度提示
success: res => {
serverIds.push(res.serverId);
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}
});
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}).then(serverIds => {
let promiseList = [];
serverIds.map(serverId =>
promiseList.push(
this.$http.get('picture/upload', {
params: {
mediaId: serverId
}
})
)
);
Promise.all(promiseList).then(result => {
let pictureIds = [];
result.map(item => pictureIds.push(item.data));
it.list.push(
'http://dj.dlbdata.cn/dangjian/picture/showThumbnail?pictureId=' + pictureIds.join()
);
it.arr.push(pictureIds.join());
});
});
}
});
}
},
mounted() {
weixin.init(['chooseImage', 'uploadImage']);
this.list1Selected = this.list1.find(item => item.moduleId == this.$route.params.moduleid);
/* this.getActivity();
this.getList();
this.getUser();*/
this.getUser1();
},
computed: {
...mapGetters(['user'])
}
};



